@import url('https://fonts.googleapis.com/css?family=Tajawal');

* {
    font-family: 'Tajawal', sans-serif;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-heart-bit infinite 3s ease-in-out;
            animation: App-logo-heart-bit infinite 3s ease-in-out;
    color: #dc5759;
}

@-webkit-keyframes App-logo-heart-bit {
    50% {
        color: #df3937;
    }
}

@keyframes App-logo-heart-bit {
    50% {
        color: #df3937;
    }
}

.App-header {
    background-color: #222;
    padding: 20px;
    color: white;
}

.App-title {
}

.App-intro {
    font-size: large;
}

#outCanvas {
    margin: 0 auto;
    display: block;
}

.ecg-result {
    word-break: break-all;
    text-align: left;
}



